body, textarea, input, select {
  background: 0;
  border-radius: 0;
  margin: 0;
  font: 16px sans-serif;
}

.smooth {
  transition: all .2s;
}

.btn, .nav a {
  text-decoration: none;
}

.container {
  width: auto;
  margin: 0 20px;
}

label > * {
  display: inline;
}

form > * {
  margin-bottom: 10px;
  display: block;
}

.btn {
  color: #fff;
  cursor: pointer;
  background: #999;
  border: none;
  border-radius: 6px;
  margin: 2px 0;
  padding: 12px 30px 14px;
  display: inline-block;
}

.btn:hover {
  background: #888;
}

.btn:active, .btn:focus {
