h1 {
  font-size: 3em;
}

.btn, h2 {
  font-size: 2em;
}

.ico {
  font: 33px Arial Unicode MS, Lucida Sans Unicode;
}

.addon, .btn-sm, .nav, textarea, input, select {
  outline: none;
  font-size: 14px;
}

textarea, input, select {
  border: 1px solid #ccc;
  padding: 8px;
}

textarea:focus, input:focus, select:focus {
  border-color: #5ab;
}

textarea, input[type="text"] {
  -webkit-appearance: none;
  width: 13em;
}

.addon {
  padding: 8px 12px;
  box-shadow: 0 0 0 1px #ccc;
}

.nav, .nav .current, .nav a:hover {
  color: #fff;
  background: #000;
}

.nav {
  height: 24px;
  padding: 11px 0 15px;
}

.nav a {
  color: #aaa;
  padding-right: 1em;
  position: relative;
  top: -1px;
}

.nav .pagename {
  font-size: 22px;
  top: 1px;
}

.btn.btn-close {
  float: right;
  background: #000;
  margin: -54px 7px;
  font-size: 25px;
  display: none;
}

@media (min-width: 1310px) {
  .container {
    width: 1270px;
    margin: auto;
  }
}

@media (max-width: 870px) {
  .row .col {
    width: 100%;
  }
}

@media (max-width: 500px) {
  .btn.btn-close {
    display: block;
  }

  .nav {
    overflow: hidden;
  }

  .pagename {
    margin-top: -11px;
  }

  .nav:active, .nav:focus {
    height: auto;
  }

  .nav div:before {
    content: "";
    float: right;
    height: 4px;
    width: 20px;
    background: #000;
    border-top: 3px solid;
    border-bottom: 10px double;
    position: relative;
    top: 14px;
    right: 3px;
  }

  .nav a {
    width: 50%;
    padding: .5em 0;
    display: block;
  }
}

.table th, .table td {
  text-align: left;
  padding: .5em;
}

.table tbody > :nth-child(2n-1) {
