}

button.strava-auth {
  width: 193px;
  height: 48px;
  background: url("btn_strava_connectwith_orange.efb4e20a.svg");
  border: none;
  margin: 10px;
  transition: background 1s;
}

button.strava-auth:hover {
  cursor: pointer;
  background: url("btn_strava_connectwith_light.084e2232.svg");
  transition: background 1s;
}

button.strava-auth:focus {
  outline: none;
  transition: transform .1s;
  transform: scale(.95);
}

button.strava-auth:focus-visible {
  outline: 1px dashed gray;
}

